// 
let h
// 
const types = ['input', 'text', 'number', 'password', 'email', 'textarea']
// el-inputslot
import { renderSlots } from '../../utils'
import SlotsMixin from '../../mixins/SlotsMixin'
export default {
  name: 'DyInput',
  mixins: [SlotsMixin],
  components: {
    // el-inputslot
    SlotContent: () => import('../../slotContent')
  },
  inheritAttrs: false,
  props: {
    // 
    type: {
      default: 'text',
      validator: typeVal => {
        if (types.includes(typeVal)) {
          return true
        } else {
          console.error(`[${types.toString()}]`)
          return false
        }
      }
    },
    // 
    value: {},
    // 
    resize: {
      type: String,
      default: 'none'
    },
    // el-autocomplete
    isAutocomplete: {
      type: Boolean,
      default: false
    },
    // elInputref
    elInputRef: {
      type: String,
      default: 'elInput'
    }

    // el-input
  },
  computed: {
    newValue: {
      get({ value }) {
        return value
      },
      set(val) {
        this.$emit('input', val)
      }
    },
    // 
    componentTag: {
      get({ isAutocomplete, $attrs }) {
        const fetchSuggestions = $attrs['fetch-suggestions']
        // fetchSuggestions (isAutocompletetrue)
        return isAutocomplete && typeof fetchSuggestions === 'function'
          ? 'el-autocomplete'
          : 'el-input'
      }
    }
  },
  methods: {
    onInputHandle(val) {
      this.newValue = val
    }
  },
  render() {
    h = this.$createElement
    const self = this
    const { onInputHandle, bindSlots, componentTag, elInputRef } = self
    // 
    const slotsVNode = renderSlots(h, bindSlots)
    return h(
      componentTag,
      {
        props: {
          type: self.type,
          value: self.newValue,
          resize: self.resize,
          ...self.$attrs
        },
        attrs: {
          ...self.$attrs
        },
        on: {
          input: onInputHandle,
          ...self.$listeners
        },
        ref: elInputRef
      },
      slotsVNode
    )
  }
}
