import RequestMixin from '../../mixins/RequestMixin'

// 
let h
// 
const types = ['el-cascader', 'el-cascader-panel']

import { renderSlots } from '../../utils'

export default {
  name: 'DyCascader',
  inheritAttrs: false,
  mixins: [RequestMixin],
  components: {
    // el-cascaderslot
    SlotContent: () => import('../../slotContent')
  },
  props: {
    // 
    type: {
      default: 'el-cascader',
      validator: typeVal => {
        if (types.includes(typeVal)) {
          return true
        } else {
          console.error(`[${types.toString()}]`)
          return false
        }
      }
    },
    // 
    value: {},
    // 
    options: {
      type: Array,
      default: () => []
    },
    // 
    formatter: Function,
    // elCascaderref
    elCascaderRef: {
      type: String,
      default: 'elCascader'
    }
    // el-cascader/el-cascader-panel
  },
  watch: {
    requestOption: {
      async handler({ url }) {
        if (!url) return
        try {
          const options = await this.getAsyncOptions()
          this.options.length = 0
          this.options.push(...options)
        } catch (e) {
          console.error(e)
        }
      },
      immediate: true
    }
  },
  methods: {
    // 
    async getAsyncOptions() {
      const options = await this.$request({
        ...this.requestOption,
        ...this.headers
      })
      return options || []
    }
  },
  render() {
    h = this.$createElement
    const self = this
    const { elCascaderRef, $slots, $scopedSlots, type, options } = self
    // el-selectslot
    const slotsVNode = renderSlots(h, $slots)

    const cascaderVNode = h(
      type,
      {
        props: {
          value: this.newValue,
          options,
          ...this.$attrs
        },
        on: {
          ...this.$listeners
        },
        scopedSlots: {
          default(props) {
            if ($scopedSlots.default) {
              return $scopedSlots.default(props)
            }
          }
        },
        ref: elCascaderRef
      },
      [].concat(slotsVNode)
    )

    return cascaderVNode
  }
}
