// 
let h
// 
const types = [
  'datetimerange',
  'daterange',
  'dates',
  'date',
  'year',
  'month',
  'week'
]
export default {
  name: 'DyDatePicker',
  // $attrsdom
  inheritAttrs: false,
  props: {
    // elDatePickerref
    elDatePickerRef: {
      type: String,
      default: 'elDatePicker'
    },
    // 
    type: {
      default: 'date',
      validator: typeVal => {
        if (types.includes(typeVal)) {
          return true
        } else {
          console.error(`[${types.toString()}]`)
          return false
        }
      }
    },
    // 
    value: {}
    // el-date-pickerprops
  },
  computed: {
    newValue: {
      get({ value }) {
        return value
      },
      set(value) {
        this.$emit('change', value)
      }
    },
    // $attrs
    extendAttrs({ $attrs, type }) {
      let _extendAttrs = {}
      if (['daterange', 'datetimerange'].includes(type)) {
        _extendAttrs = {
          'range-separator': '',
          'start-placeholder': '',
          'end-placeholder': ''
        }
      }

      return { ..._extendAttrs, ...$attrs }
    }
  },
  methods: {
    onChangeHandle(val) {
      this.newValue = val
    }
  },
  render($createElement) {
    h = $createElement
    const self = this
    const { onChangeHandle, extendAttrs } = this
    return h('el-date-picker', {
      props: {
        value: self.newValue,
        ...extendAttrs,
        ...self.$props
      },
      staticClass: 'dynamic-date-picker',
      on: {
        input: onChangeHandle,
        ...self.$listeners
      },
      ref: self.elDatePickerRef
    })
  }
}
